* {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 12px;
  font-weight: 300;
  color: #ccc;
  background-color: #101010; 
  background-image: linear-gradient(#101010 0, #101010 8px, #0c0c0c 8px, #0c0c0c 16px);
  background-size: 100% 16px;
  background-position: 0 -4px;
  height: 100%;
}

body {
  padding: 16px;
  box-sizing: border-box;
  height: 100%;
}

input, button {
  outline: 0;
}

input[type="text"], input:not([type]) {
  border: 1px solid transparent;
  background-color: transparent;
  color: inherit;
  font: inherit;
}

input[type="text"]:not(:disabled):not(:focus):hover,
input:not([type]):not(:disabled):not(:focus):hover,
{
  border-color: #444;
}

input[type="text"]:focus,
input:not([type]):focus,
{
  color: #ccc;
  border-color: #666;
  background-color: #111;
  box-shadow:
    -1px -1px 0.5px rgba(255, 255, 255, 0.1) inset,
    1px 1px 2px rgba(0, 0, 0, 1.0) inset;
}

input[type="text"].ng-invalid,
input:not([type]).ng-invalid,
{
  border-color: #c44;
}

input[type="submit"] {
  width: 100%;
  cursor: pointer;
  border: 0;
  outline: 0;
}

ul, ol, li {
  list-style-type: none;
}

strong {
  font-weight: normal;
  color: #eee;
}

.errorbar {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 8px;
  font-size: 16px;
  line-height: 24px;
  z-index: 1000;
  margin-bottom: 16px;
  border-radius: 16px;
  text-align: center;
}

.errorbar-panic {
  background: #f44;
  color: #fff;
}

.errorbar-warning {
  background: #db4;
  color: #333;
}

.github {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  width: 149px;
  height: 149px;
  background-image: url(https://camo.githubusercontent.com/38ef81f8aca64bb9a64448d0d70f1308ef5341ab/68747470733a2f2f73332e616d617a6f6e6177732e636f6d2f6769746875622f726962626f6e732f666f726b6d655f72696768745f6461726b626c75655f3132313632312e706e67);
  text-indent: -9999px;
  opacity: 0.3;
  background-position: 10px -10px;
  background-repeat: no-repeat;
  transition: background-position 0.05s linear, opacity 0.05s linear;
}

.github:hover {
  opacity: 0.8;
  background-position: 0 0;
  z-index: 1;
  transition: background-position 0.2 linear, opacity 0.2 linear;
}

.github:active {
  opacity: 1.0;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  padding: 8px;
  position: relative;
  box-sizing: border-box;
  width: 960px;
  height: 100%;
  min-height: 320px;
  margin: 0 auto;
  border-radius: 16px;
  background: #141218;
  box-shadow:
    0 0 8px #000,
    2px 4px 8px #000,
    1px 2px 4px rgba(255, 255, 255, 0.05) inset,
    -1px -2px 4px rgba(0, 0, 0, 0.1) inset;
}

.topbar {
  display: -webkit-flex;
  width: 100%;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  height: 47px;
  margin-bottom: 8px;
  -webkit-flex-grow: 0;
  flex-grow: 0;
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-grow: 1;
  flex-grow: 1;
}

.pane {
  position: relative;
  background: #040404;
  box-shadow:
    0.5px 1px 1px rgba(0, 0, 0, 1.0) inset,
