.tooltip {
  position: absolute;
  left: 105%;
  top: 60%;
  z-index: 9999;
  border-radius: 16px;
  box-shadow: 4px 4px 16px rgba(0, 0, 0, 1);
  opacity: 0;
  visibility: hidden;
