  background-color: #111;
  color: #888;
  box-shadow:
    2px 2px 4px rgba(255, 255, 255, 0.05) inset,
    -2px -2px 4px rgba(0, 0, 0, 0.4) inset;

  transition: color 0.1s linear, background-color 0.1s linear;

  -webkit-flex-grow: 1;
  flex-grow: 1;
}

.button-checked {
  background-color: #222;
  color: #aaa;
